<template>
  <div class="user">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row :gutter="30">
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getUserList">
            <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="8">
          <el-button type="primary" @click="addClick"></el-button>
        </el-col>
      </el-row>
      <el-dialog
        :title="isTitle ? '' : ''"
        :visible.sync="isDialogVisible"
        width="30%"
        @close="closeDialog"
      >
        <el-form :model="addForm" :rules="rulesForm" ref="refForm" label-width="100px">
          <el-form-item label="" prop="username">
            <el-input v-model="addForm.username" :disabled="isTitle ? false : true"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password" v-if="isTitle">
            <el-input v-model="addForm.password"></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model="addForm.email"></el-input>
          </el-form-item>
          <el-form-item label="" prop="mobile">
            <el-input v-model="addForm.mobile"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="isDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addUser" v-if="isTitle"> </el-button>
          <el-button type="primary" @click="editUser" v-if="!isTitle"> </el-button>
        </span>
      </el-dialog>
      <el-table :data="userList" stripe border style="width: 100%">
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="username" label=""></el-table-column>
        <el-table-column prop="email" label=""></el-table-column>
        <el-table-column prop="mobile" label=""></el-table-column>
        <el-table-column prop="role_name" label=""></el-table-column>
        <el-table-column prop="mg_state" label="">
          <template slot-scope="scope">
            <el-switch v-model="scope.row.mg_state" @change="changeUserInfoState(scope.row)"></el-switch>
          </template>
        </el-table-column>
        <el-table-column label="" width="180">
          <template v-slot="scope">
            <!-- {{scope.row}} -->
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="editClick(scope.row.id)"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="deleteClick(scope.row.id)"
            ></el-button>
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="setRolesClick(scope.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 4, 6]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
      <el-dialog title="" :visible.sync="dialogVisible"
       width="50%"
       @close="clearval"
       >
        <div>
          <p>{{userInfo.username}}</p>
          <p>{{userInfo.role_name}}</p>
          <p>
            <el-select v-model="selectRolesId" placeholder="">
                <el-option
                  v-for="item in rolesList"
                  :key="item.id"
                  :label="item.roleName"
                  :value="item.id">
                </el-option>
            </el-select>
          </p>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="setRolse"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    var checkEmail = (rule, value, callback) => {
      const regEmail = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
      if (regEmail.test(value)) return callback();
      callback(new Error(""));
    };
    var checkMobile = (rule, value, callback) => {
      const regMobile = /^1[3,5,8,7]\d{9}$/;
      if (regMobile.test(value)) return callback();
      callback(new Error(""));
    };
    return {
      queryInfo: {
        query: "",
        pagenum: 1,
        pagesize: 2
      },
      userList: [],
      userInfo: {},
      rolesList:[],
      selectRolesId:'',
      total: 0,
      isDialogVisible: false,
      dialogVisible: false,
      isTitle: true,
      //   editDialogVisible:false,
      addForm: {
        username: "",
        password: "",
        email: "",
        mobile: ""
      },
      rulesForm: {
        username: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 10, message: " 3  10 ", trigger: "blur" }
        ],
        password: [
          { required: true, message: "", trigger: "blur" },
          { min: 6, max: 10, message: " 6  10 ", trigger: "blur" }
        ],
        email: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkEmail, trigger: "blur" }
        ],
        mobile: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkMobile, trigger: "blur" }
        ]
      }
    };
  },
  created() {
    this.getUserList();
  },
  methods: {
    async getUserList() {
      const { data: res } = await this.$axios.get("users", {
        params: this.queryInfo
      });
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg);
      this.userList = res.data.users;
      this.total = res.data.total;
        // console.log(this.userList);
    },
    handleSizeChange(newSize) {
      // console.log(newSize);
      this.queryInfo.pagesize = newSize;
      this.getUserList();
    },
    handleCurrentChange(newPage) {
      // console.log(newPage);
      this.queryInfo.pagenum = newPage;
      this.getUserList();
    },
    async changeUserInfoState(userInfo) {
      // console.log(userInfo);
      const { data: res } = await this.$axios.put(
        `users/${userInfo.id}/state/${userInfo.mg_state}`
      );
      if (res.meta.status !== 200) {
        // userInfo.mg_state = !userInfo.mg_state;
        return this.$message.error("" + res.meta.msg);
      }
      this.$message.success(res.meta.msg);
    },
    closeDialog() {
      this.$refs.refForm.resetFields();
    },
    addClick() {
      this.isTitle = true;
      this.isDialogVisible = true;
    },
    addUser() {
      this.$refs.refForm.validate(async valid => {
        // console.log(valid);
        if (!valid) return;
        const { data: res } = await this.$axios.post("users", this.addForm);
        // console.log(res);
        if (res.meta.status !== 201)
          return this.$message.error("" + res.meta.msg);
        this.isDialogVisible = false;
        this.getUserList();
        this.$message.success(res.meta.msg);
      });
    },
    async editClick(id) {
      // console.log(id)
      this.isTitle = false;
      this.isDialogVisible = true;
      const { data: res } = await this.$axios.get("users/" + id);
      // console.log(res);
      if (res.meta.status !== 200)
        return this.$message.error("" + res.meta.msg);
      this.addForm = res.data;
    },
    async editUser() {
      this.$refs.refForm.validate(async valid => {
        if (!valid) return;
        const { data: res } = await this.$axios.put(
          "users/" + this.addForm.id,
          {
            email: this.addForm.email,
            mobile: this.addForm.mobile
          }
        );
        // console.log(res);
        if (res.meta.status !== 200)
          return this.$message.error("" + res.meta.msg);
        this.isDialogVisible = false;
        this.getUserList();
        this.$message.success(res.meta.msg);
      });
    },
    deleteClick(id) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(async () => {
          const { data: res } = await this.$axios.delete("users/" + id);
          //   console.log(res);
          if (res.meta.status !== 200)
            return this.$message.error("" + res.meta.msg);
          this.getUserList();
          this.$message.success(res.meta.msg);
        })
        .catch(() => {
          this.$message.info("");
        });
    },
    async setRolesClick(val) {
      this.userInfo = val;
      const {data:res} = await this.$axios.get("roles");
      if (res.meta.status !== 200)
      return this.$message.error("" + res.meta.msg);
      this.rolesList = res.data;
      this.dialogVisible = true;
    },
    async setRolse(){
      if(!this.selectRolesId) return this.$message.error("");
      const {data:res} = await this.$axios.put(`users/${this.userInfo.id}/role`,{rid:this.selectRolesId});
      if (res.meta.status !== 200)
      return this.$message.error("" + res.meta.msg);
      this.$message.success(res.meta.msg);
      this.getUserList();
      this.dialogVisible = false;
    },
    clearval(){
      this.selectRolesId = '';
      this.userInfo = {};
    }
  }
};
</script>
<style lang="less">
.el-table {
  margin-top: 20px;
}
.el-pagination {
  margin-top: 20px;
}
</style>