<template>
    <div class="login">
        <div class="login_box">
            <div class="avatar_box">
                <img src="../assets/logo.png" alt="">
            </div>
            <el-form :model="loginForm" :rules="rules" ref="ruleForm" class="form_box">
                <el-form-item prop="username">
                    <el-input v-model="loginForm.username" prefix-icon="el-icon-user"></el-input>
                </el-form-item>
                <el-form-item prop="password">
                    <el-input v-model="loginForm.password" type="password" prefix-icon="el-icon-lock"></el-input>
                </el-form-item>
                <el-row class="btn">
                    <el-button type="primary" @click="submitForm"></el-button>
                    <el-button type="info" @click="resetForm"></el-button>
                </el-row>
            </el-form>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            loginForm:{
                username:'admin',
                password:'123456',
            },
            rules: {
                username: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
                ],
                password: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 6, max: 10, message: ' 6  10 ', trigger: 'blur' }
                ],
            }
        }
    },
    methods:{
        submitForm(){
            // console.log(this.$refs.ruleForm);
            this.$refs.ruleForm.validate(async valid =>{
                // console.log(valid);
                if(!valid) return;
                const { data: res } = await this.$axios.post("login",this.loginForm);
                // console.log(res)
                if( res.meta.status !== 200) return this.$message.error(''+res.meta.msg);
                this.$message.success(res.meta.msg);
                // console.log(res.token);
                window.sessionStorage.setItem('token',res.data.token);
                this.$router.push('/home');
            });
        },
        resetForm(){
            this.$refs.ruleForm.resetFields();
            // console.log('this',this);
            // console.log('this.$refs',this.$refs);
            // console.log('this.$refs.ruleForm',this.$refs.ruleForm);
        }
    }
    
}
</script>
<style lang="less" scoped>
    .login{
        background: #2b4b6b;
        height: 100%;
    }
    .login_box{
        width: 450px;
        height: 300px;
        background-color: white;
        border-radius: 3px;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }
    .avatar_box{
        width: 130px;
        height: 130px;
        padding: 8px;
        border: 1px solid #eee;
        border-radius: 50%;
        background-color: white;
        box-shadow: 0 0 8px #ddd;
        position: absolute;
        left: 50%;
        transform: translate(-50%,-50%);
        img{
            width: 100%;
            height: 100%;
            border-radius: 50%;
            background-color:#eee;
        }
    }
    .form_box{
        position: absolute;
        bottom: 0;
        padding: 20px;
        width: 100%;
        box-sizing: border-box;
    }
    .btn{
        display: flex;
        justify-content: space-between;
        // justify-content: space-around;
        // justify-content: space-evenly;
        // justify-content: flex-end;

    }
</style>