<template>
  <div class="user">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row :gutter="30">
        <el-col :span="8">
          <el-button type="primary" @click="addClick"></el-button>
        </el-col>
      </el-row>
      <el-dialog
        :title="isTitle ? '' : ''"
        :visible.sync="isDialogVisible"
        width="30%"
        @close="closeDialog"
      >
        <el-form :model="addForm" :rules="rulesForm" ref="refForm" label-width="100px">
          <el-form-item label="" prop="roleName">
            <el-input v-model="addForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="roleDesc">
            <el-input v-model="addForm.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="isDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addUser" v-if="isTitle"> </el-button>
          <el-button type="primary" @click="editUser" v-if="!isTitle"> </el-button>
        </span>
      </el-dialog>
      <el-table :data="rolesList" stripe border style="width: 100%">
        <el-table-column type="expand">
          <template v-slot="prop">
            <el-row
              :class="['bdBottom', i1 === 0 ? 'bdTop' : '','vcenter']"
              v-for="(item1,i1) in prop.row.children"
              :key="item1.id"
            >
              <el-col :span="5">
                <el-tag closable @close="removeRightById(prop.row,item1.id)">{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <el-col :span="19">
                <el-row
                  :class="[i2 === 0 ? '' : 'bdTop','vcenter']"
                  v-for="(item2,i2) in item1.children"
                  :key="item2.id"
                >
                  <el-col :span="6">
                    <el-tag
                      closable
                      type="success"
                      @close="removeRightById(prop.row,item2.id)"
                    >{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag
                      v-for="item3 in item2.children"
                      :key="item3.id"
                      closable
                      type="warning"
                      @close="removeRightById(prop.row,item3.id)"
                    >{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="roleName" label=""></el-table-column>
        <el-table-column prop="roleDesc" label=""></el-table-column>
        <el-table-column label="" width="300">
          <template v-slot="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="editClick(scope.row.id)"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="deleteClick(scope.row.id)"
            ></el-button>
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="warning" icon="el-icon-setting" size="mini" @click="dialogClick(scope.row)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <el-dialog title="" :visible.sync="dialogVisible" @close="closeRightsDialog" width="50%">
        <el-tree
          :data="rightsList"
          :props="defaultProps"
          show-checkbox
          node-key="id"
          ref="tree"
          default-expand-all
          :default-checked-keys = "defRightsId"
        ></el-tree>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="setRights"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      rolesList: [],
      rightsList: [],
      roleId:'',
      defaultProps: {
          children: 'children',
          label: 'authName'
      },
      defRightsId:[],
      isDialogVisible: false,
      dialogVisible: false,
      isTitle: true,
      addForm: {
        roleName: "",
        roleDesc: ""
      },
      rulesForm: {
        roleName: [
          { required: true, message: "", trigger: "blur" }
          // { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ],
        roleDesc: [
          { required: true, message: "", trigger: "blur" }
          // { min: 6, max: 10, message: ' 6  10 ', trigger: 'blur' }
        ]
      }
    };
  },
  created() {
    this.getRolesList();
  },
  methods: {
    async getRolesList() {
      const { data: res } = await this.$axios.get("roles");
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg);
      this.rolesList = res.data;
      // console.log(this.rolesList);
    },

    closeDialog() {
      this.$refs.refForm.resetFields();
    },
    addClick() {
      this.isTitle = true;
      this.isDialogVisible = true;
    },
    addUser() {
      this.$refs.refForm.validate(async valid => {
        // console.log(valid);
        if (!valid) return;
        const { data: res } = await this.$axios.post("roles", this.addForm);
        console.log(res);
        if (res.meta.status !== 201)
          return this.$message.error("" + res.meta.msg);
        this.isDialogVisible = false;
        this.getRolesList();
        this.$message.success(res.meta.msg);
      });
    },
    async editClick(id) {
      // console.log(id)
      this.isTitle = false;
      this.isDialogVisible = true;
      const { data: res } = await this.$axios.get("roles/" + id);
      // console.log(res);
      if (res.meta.status !== 200)
        return this.$message.error("" + res.meta.msg);
      this.addForm = res.data;
    },
    async editUser() {
      this.$refs.refForm.validate(async valid => {
        if (!valid) return;
        const { data: res } = await this.$axios.put(
          "roles/" + this.addForm.attr_id,
          {
            roleName: this.addForm.roleName,
            roleDesc: this.addForm.roleDesc
          }
        );
        console.log(res);
        if (res.meta.status !== 200)
          return this.$message.error("" + res.meta.msg);
        this.isDialogVisible = false;
        this.getRolesList();
        this.$message.success(res.meta.msg);
      });
    },
    deleteClick(id) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(async () => {
          const { data: res } = await this.$axios.delete("roles/" + id);
          //   console.log(res);
          if (res.meta.status !== 200)
            return this.$message.error("" + res.meta.msg);
          this.getRolesList();
          this.$message.success(res.meta.msg);
        })
        .catch(() => {
          this.$message.info("");
        });
    },
    async dialogClick(role) {
      this.roleId = role.id;
      this.dialogVisible = true;
      const { data: res } = await this.$axios.get("rights/tree");
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg);
      this.rightsList = res.data;
      // console.log(this.rightsList);
      this.getDefRightsId(role,this.defRightsId);
    },
    removeRightById(role, rightId) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(async () => {
          const { data: res } = await this.$axios.delete(
            `roles/${role.id}/rights/${rightId}`
          );
          console.log(res);
          if (res.meta.status !== 200)
            return this.$message.error("" + res.meta.msg);
          role.children = res.data;
          this.$message.success(res.meta.msg);
        })
        .catch(() => {
          this.$message.info("");
        });
    },
    getDefRightsId(node,arr){
      if(!node.children) return arr.push(node.id);
      node.children.forEach(item => this.getDefRightsId(item,arr));
    },
    closeRightsDialog(){
      this.defRightsId = [];
    },
    async setRights(){
      const keys = [
        ...this.$refs.tree.getCheckedKeys(),
        ...this.$refs.tree.getHalfCheckedKeys()
      ]
      const rightsIdStr = keys.join(',');
      console.log(rightsIdStr);
      const {data:res} = await this.$axios.post(`roles/${this.roleId}/rights`,{rids:rightsIdStr});
      console.log(res);
      if (res.meta.status !== 200)
      return this.$message.error("" + res.meta.msg);
      this.getRolesList();
      this.dialogVisible = false;
      this.$message.success("");
    }
  }
};
</script>
<style lang="less" scoped>
.bdTop {
  border-top: 1px solid #eee;
}
.bdBottom {
  border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>