<template>
  <div class="cate_params">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-alert title="" type="warning" :closable="false" show-icon></el-alert>
      <el-row class="cat_opt">
        <el-col>
          <span>
            
            <el-cascader
              v-model="cateKeys"
              :options="cateList"
              :props="cateProps"
              @change="handleChange"
              clearable
            ></el-cascader>
          </span>
        </el-col>
      </el-row>
      <el-tabs v-model="activeTabName" @tab-click="handleTabClick">
        <el-tab-pane label="" name="many">
          <el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addClick"></el-button>
          <el-table :data="manyTableData" border stripe>
            <el-table-column type="expand">
              <template v-slot="prop">
                <el-tag
                  v-for="(item,index) in prop.row.attr_vals"
                  :key="index"
                  closable
                  @close="removeTag(index,prop.row)"
                >{{item}}</el-tag>
                <el-input
                  class="input-new-tag"
                  v-if="prop.row.inputVisible"
                  v-model="prop.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(prop.row)"
                  @blur="handleInputConfirm(prop.row)"
                ></el-input>
                <el-button v-else size="small" @click="showInput(prop.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column label="" prop="attr_name"></el-table-column>
            <el-table-column label="" width="300">
              <template v-slot="scope">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  @click="editClick(scope.row.attr_id)"
                ></el-button>
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="deleteClick(scope.row.attr_id)"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="" name="only">
          <el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addClick"></el-button>
          <el-table :data="onlyTableData" border stripe>
            <el-table-column type="expand">
              <template v-slot="prop">
                <el-tag
                  v-for="(item,index) in prop.row.attr_vals"
                  :key="index"
                  closable
                  @close="removeTag(index,prop.row)"
                >{{item}}</el-tag>
                <el-input
                  class="input-new-tag"
                  v-if="prop.row.inputVisible"
                  v-model="prop.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(prop.row)"
                  @blur="handleInputConfirm(prop.row)"
                ></el-input>
                <el-button v-else size="small" @click="showInput(prop.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column label="" prop="attr_name"></el-table-column>
            <el-table-column label="" width="300">
              <template v-slot="scope">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  @click="editClick(scope.row.attr_id)"
                ></el-button>
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="deleteClick(scope.row.attr_id)"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
      <el-dialog
        :title="headTitle+titleText"
        :visible.sync="dialogVisible"
        width="50%"
        @close="closeDialog"
      >
        <el-form :model="addForm" :rules="rulesForm" ref="refForm" label-width="100px">
          <el-form-item :label="titleText" prop="attr_name">
            <el-input v-model="addForm.attr_name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="addParams" v-if="isBtn"> </el-button>
          <el-button type="primary" @click="editParams" v-if="!isBtn"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      cateList: [],
      cateKeys: [],
      isBtn: true,
      addForm: {
        attr_name: ""
      },
      rulesForm: {
        attr_name: [
          { required: true, message: "", trigger: "blur" }
          // { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ]
      },
      manyTableData: [],
      onlyTableData: [],
      dialogVisible: false,
      activeTabName: "many",
      cateProps: {
        expandTrigger: "hover",
        value: "cat_id",
        label: "cat_name",
        children: "children"
      },
      headTitle: ""
    };
  },
  created() {
    this.getCateList();
  },
  computed: {
    isBtnDisabled() {
      if (this.cateKeys.length !== 3) return true;
      return false;
    },
    catId() {
      if (this.cateKeys.length !== 3) return null;
      return this.cateKeys[2];
    },
    titleText() {
      if (this.activeTabName === "many") {
        return "";
      } else {
        return "";
      }
    }
    // headTitle(){
    //     if(this.isBtn=true){
    //         return "";
    //     }else{
    //         return "";
    //     }
    // }
  },
  methods: {
    async getCateList() {
      const { data: res } = await this.$axios.get("categories");
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg);
      this.cateList = res.data;
      //   console.log(this.cateList);
    },
    async getParamsData() {
      if (this.cateKeys.length !== 3) {
        this.cateKeys = [];
        this.manyTableData = [];
        this.onlyTableData = [];
        return;
      }
      //   console.log(this.catId);
      const { data: res } = await this.$axios.get(
        `categories/${this.catId}/attributes`,
        {
          params: { sel: this.activeTabName }
        }
      );
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg);
      res.data.forEach(item => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(" ") : [];
        item.inputVisible = false;
        item.inputValue = "";
      });
      console.log(res.data);
      if (this.activeTabName === "many") {
        this.manyTableData = res.data;
      } else {
        this.onlyTableData = res.data;
      }
    },
    handleChange() {
      this.getParamsData();
    },
    handleTabClick() {
      //   console.log(this.activeTabName);
      this.getParamsData();
    },
    addClick() {
      this.headTitle = "";
      this.isBtn = true;
      this.dialogVisible = true;
    },
    closeDialog() {
      this.$refs.refForm.resetFields();
    },
    addParams() {
      this.$refs.refForm.validate(async valid => {
        // console.log(valid);
        if (!valid) return;
        const { data: res } = await this.$axios.post(
          `categories/${this.catId}/attributes`,
          {
            attr_name: this.addForm.attr_name,
            attr_sel: this.activeTabName
          }
        );
        // console.log(res);
        if (res.meta.status !== 201)
          return this.$message.error("" + res.meta.msg);
        this.dialogVisible = false;
        this.getParamsData();
        this.$message.success(res.meta.msg);
      });
    },
    async editClick(id) {
      this.headTitle = "";
      this.isBtn = false;
      //   console.log(id);
      this.dialogVisible = true;
      const { data: res } = await this.$axios.get(
        `categories/${this.catId}/attributes/${id}`,
        {
          attr_sel: this.activeTabName
        }
      );
      //   console.log(res);
      if (res.meta.status !== 200)
        return this.$message.error("" + res.meta.msg);
      this.addForm = res.data;
    },
    async editParams() {
      this.$refs.refForm.validate(async valid => {
        if (!valid) return;
        const { data: res } = await this.$axios.put(
          `categories/${this.catId}/attributes/` + this.addForm.attr_id,
          {
            attr_name: this.addForm.attr_name,
            attr_sel: this.activeTabName
          }
        );
        // console.log(res);
        if (res.meta.status !== 200)
          return this.$message.error("" + res.meta.msg);
        this.dialogVisible = false;
        this.getParamsData();
        this.$message.success(res.meta.msg);
      });
    },
    deleteClick(id) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(async () => {
          const { data: res } = await this.$axios.delete(
            `categories/${this.catId}/attributes/` + id
          );
          //   console.log(res);
          if (res.meta.status !== 200)
            return this.$message.error("" + res.meta.msg);
          this.getParamsData();
          this.$message.success(res.meta.msg);
        })
        .catch(() => {
          this.$message.info("");
        });
    },
    showInput(val) {
      val.inputVisible = true;
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    async saveAttrData(val) {
      const { data: res } = await this.$axios.put(
        `categories/${this.catId}/attributes/` + val.attr_id,
        {
          attr_name: val.attr_name,
          attr_sel: val.attr_sel,
          attr_vals: val.attr_vals.join(" ")
        }
      );
      if (res.meta.status !== 200)
        return this.$message.error("" + res.meta.msg);
      this.$message.success(res.meta.msg);
    },
    handleInputConfirm(val) {
      if (val.inputValue.trim().length === 0) {
        val.inputValue = "";
        val.inputVisible = false;
        return;
      }
      val.attr_vals.push(val.inputValue.trim());
      val.inputValue = "";
      val.inputVisible = false;
      this.saveAttrData(val);
    },
    removeTag(index, val) {
      val.attr_vals.splice(index, 1);
      this.saveAttrData(val);
    }
  }
};
</script>
<style lang="less" scoped>
.cat_opt {
  margin: 15px 0;
}
.input-new-tag {
  width: 120px;
}
</style>