<template>
  <div class="cate">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row :gutter="30">
        <el-col :span="8">
          <el-button type="primary" @click="addClick"></el-button>
        </el-col>
      </el-row>
      <el-dialog
        :title="isTitle ? '' : ''"
        :visible.sync="isDialogVisible"
        width="30%"
        @close="closeDialog"
      >
        <el-form :model="cateForm" :rules="rulesForm" ref="refForm" label-width="100px">
          <el-form-item label=":" prop="cat_name">
            <el-input v-model="cateForm.cat_name"></el-input>
          </el-form-item>
          <el-form-item label=":" v-if="isTitle">
            <el-cascader
                style="width:100%"
              v-model="parentCateKeys"
              :options="parentCateList"
              :props="parentCateProps"
              @change="handleChange"
              clearable
              change-on-select
            ></el-cascader>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="isDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addCate" v-if="isTitle"> </el-button>
          <el-button type="primary" @click="editCate" v-if="!isTitle"> </el-button>
        </span>
      </el-dialog>
      <tree-table
        style="margin-top:20px"
        :data="cateList"
        :columns="columns"
        show-index
        index-text="#"
        :expand-type="false"
        :selection-type="false"
        border
      >
        <template slot="isOk" slot-scope="scope">
          <i
            :class="scope.row.cat_deleted ? 'el-icon-success' : 'el-icon-error' "
            :style="scope.row.cat_deleted ? 'color:lightgreen' : 'color:red'"
          ></i>
        </template>
        <template slot="isLevel" slot-scope="scope">
          <el-tag v-if="scope.row.cat_level == '0'"></el-tag>
          <el-tag type="success" v-else-if="scope.row.cat_level == '1'"></el-tag>
          <el-tag type="warning" v-else></el-tag>
        </template>
        <template slot="isMake" slot-scope="scope">
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click="editClick(scope.row.cat_id)"
          ></el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            @click="deleteClick(scope.row.cat_id)"
          ></el-button>
        </template>
      </tree-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 4, 6]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      parentCateKeys:[],
      parentCateList:[],
      parentCateProps:{
          expandTrigger: 'hover',
        //   checkStrictly:true,
          value:'cat_id',
          label:'cat_name',
          children:'children'
      },
      queryInfo: {
        type: [3],
        pagenum: 1,
        pagesize: 5
      },
      cateForm: {
        cat_name: "",
        cat_pid: 0,
        cat_level: 0
      },
      rulesForm: {
        cat_name: [
          { required: true, message: "", trigger: "blur" }
          // { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ]
      },
      cateList: [],
      total: 0,
      isTitle: true,
      isDialogVisible: false,

      columns: [
        {
          label: "",
          prop: "cat_name",
          width: "400px"
        },
        {
          label: "",
          type: "template",
          template: "isOk",
          minWidth: "50px"
        },
        {
          label: "",
          type: "template",
          template: "isLevel",
          minWidth: "50px"
        },
        {
          label: "",
          type: "template",
          template: "isMake",
          minWidth: "100px"
        }
      ]
    };
  },
  created() {
    this.getCategories();
  },
  methods: {
    async getCategories() {
      const { data: res } = await this.$axios.get("categories", {
        params: this.queryInfo
      });
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg);
      this.cateList = res.data.result;
      this.total = res.data.total;
      console.log(this.cateList);
    },
    handleSizeChange(newSize) {
      // console.log(newSize);
      this.queryInfo.pagesize = newSize;
      this.getCategories();
    },
    handleCurrentChange(newPage) {
      // console.log(newPage);
      this.queryInfo.pagenum = newPage;
      this.getCategories();
    },
    closeDialog() {
      this.$refs.refForm.resetFields();
      this.parentCateKeys = [];
      this.cateForm.cat_level = 0;
      this.cateForm.cat_pid = 0;
    },
    addClick() {
      this.getParentCateList();
      this.isTitle = true;
      this.isDialogVisible = true;
    },
    changeUserInfoState(val) {},
    async editClick(id) {
        this.isTitle = false;
      this.isDialogVisible = true;
      const { data: res } = await this.$axios.get("categories/" + id);
      // console.log(res);
      if (res.meta.status !== 200)
      return this.$message.error("" + res.meta.msg);
      this.cateForm = res.data;
      console.log(this.cateForm)
    },
    async getParentCateList(val) {
      const { data: res } = await this.$axios.get("categories", {
        params: { type: 2 }
      });
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg);
      this.parentCateList = res.data;
    //   console.log(this.parentCateList);
    },
    handleChange(val){
        // console.log(this.parentCateKeys)
        if(this.parentCateKeys.length > 0){
            this.cateForm.cat_pid = this.parentCateKeys[this.parentCateKeys.length-1];
            this.cateForm.cat_level = this.parentCateKeys.length;
        }else{
            this.cateForm.cat_pid = 0;
            this.cateForm.cat_level = 0;
        }
    },
    addCate(val){
        this.$refs.refForm.validate(async valid => {
        if (!valid) return;
        const { data: res } = await this.$axios.post("categories", this.cateForm);
        console.log(res);
        if (res.meta.status !== 201)
          return this.$message.error("" + res.meta.msg);
        this.isDialogVisible = false;
        this.getCategories();
        this.$message.success(res.meta.msg);
      });
    },
    async editCate() {
      this.$refs.refForm.validate(async valid => {
        if (!valid) return;
        const { data: res } = await this.$axios.put(
          "categories/" + this.cateForm.cat_id,
          {
            cat_name: this.cateForm.cat_name,
          }
        );
        console.log(res);
        if (res.meta.status !== 200)
          return this.$message.error("" + res.meta.msg);
        this.isDialogVisible = false;
        this.getCategories();
        this.$message.success(res.meta.msg);
      });
    },
    deleteClick(id) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(async () => {
          const { data: res } = await this.$axios.delete("categories/" + id);
          //   console.log(res);
          if (res.meta.status !== 200)
            return this.$message.error("" + res.meta.msg);
          this.getCategories();
          this.$message.success(res.meta.msg);
        })
        .catch(() => {
          this.$message.info("");
        });
    },
  }
};
</script>
<style lang="less">
 
</style>