<template>
  <div class="rights">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
        <el-table :data="rightList" stripe border style="width: 100%">
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column prop="authName" label=""></el-table-column>
            <el-table-column prop="path" label=""></el-table-column>
            <el-table-column label="">
                <template v-slot="scope">
                    <el-tag v-if="scope.row.level == '0'"></el-tag>
                    <el-tag type="success" v-else-if="scope.row.level == '1'"></el-tag>
                    <el-tag type="warning" v-else></el-tag>
                </template>
            </el-table-column>
        </el-table>
    </el-card>
  </div>
</template>
<script>
export default {
    data(){
        return{
            rightList:[],
        }
    },
    created() {
        this.getRightList();
    },
    methods:{
        async getRightList(){
            const { data: res } = await this.$axios.get('rights/list');
            if (res.meta.status !== 200) return this.$message.error(res.meta.msg);
            this.rightList = res.data;
            console.log(this.rightList)
        }
    }
};
</script>
