<template>
  <div class="user">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row :gutter="30">
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getGoods_List">
            <el-button slot="append" icon="el-icon-search" @click="getGoods_List"></el-button>
          </el-input>
        </el-col>
        <el-col :span="8">
          <el-button type="primary" @click="addClick"></el-button>
        </el-col>
      </el-row>
      <el-table :data="goods_List" stripe border style="width: 100%">
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="goods_name" label=""></el-table-column>
        <el-table-column width="120" prop="goods_price" label=""></el-table-column>
        <el-table-column width="100" prop="goods_weight" label=""></el-table-column>
        <el-table-column width="160" label="">
            <template v-slot="scope">
                {{scope.row.add_time | dataFormat}}
            </template>
        </el-table-column>
        <el-table-column label="" width="130">
          <template v-slot="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="editClick(scope.row.goods_id)"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="deleteClick(scope.row.goods_id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 4, 6]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      queryInfo: {
        query: "",
        pagenum: 1,
        pagesize: 2
      },
      goods_List: [],
      total: 0,
      isDialogVisible: false,
      isTitle: true,
      addForm: {
        username: "",
        password: "",
      },
      rulesForm: {
        username: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 10, message: " 3  10 ", trigger: "blur" }
        ],
        password: [
          { required: true, message: "", trigger: "blur" },
          { min: 6, max: 10, message: " 6  10 ", trigger: "blur" }
        ],
      }
    };
  },
  created() {
    this.getGoods_List();
  },
  methods: {
    async getGoods_List() {
      const { data: res } = await this.$axios.get("goods", {
        params: this.queryInfo
      });
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg);
    //   console.log(res.data);
      this.goods_List = res.data.goods;
      this.total = res.data.total;
      console.log(this.goods_List);
    },
    handleSizeChange(newSize) {
      // console.log(newSize);
      this.queryInfo.pagesize = newSize;
      this.getGoods_List();
    },
    handleCurrentChange(newPage) {
      // console.log(newPage);
      this.queryInfo.pagenum = newPage;
      this.getGoods_List();
    },
    closeDialog() {
      this.$refs.refForm.resetFields();
    },
    addClick() {
        this.$router.push('/goods/add');
    },
    
    editClick(id) {
      this.$router.push({
        path:'/goods/add',
        query:{id:id}
      });
    },
    deleteClick(id) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(async () => {
          const { data: res } = await this.$axios.delete("goods/" + id);
          //   console.log(res);
          if (res.meta.status !== 200)
            return this.$message.error("" + res.meta.msg);
          this.getGoods_List();
          this.$message.success(res.meta.msg);
        })
        .catch(() => {
          this.$message.info("");
        });
    },
  }
};
</script>
<style lang="less">
.el-table {
  margin-top: 20px;
}
.el-pagination {
  margin-top: 20px;
}
</style>