<template>
  <div class="addGoods">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-alert title="" type="info" :closable="false" show-icon center></el-alert>
      <el-steps :active="activeIndex-0" finish-status="success" align-center>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>
      <el-form :model="addForm" :rules="rulesForm" ref="refForm" label-position="top">
        <el-tabs
          v-model="activeIndex"
          :tab-position="'left'"
          :before-leave="beforeTabLeave"
          @tab-click="tabClick"
        >
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input type="number" v-model="addForm.goods_price"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input type="number" v-model="addForm.goods_weight"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input type="number" v-model="addForm.goods_number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_cat">
              <el-cascader
                v-model="addForm.goods_cat"
                :options="cateList"
                :props="cateProps"
                @change="handleChange"
                clearable
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <el-form-item :label="item.attr_name" v-for="item in manyTableData" :key="item.attr_id">
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox
                  border
                  :label="item1"
                  v-for="(item1,index) in item.attr_vals"
                  :key="index"
                ></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item :label="item.attr_name" v-for="item in onlyTableData" :key="item.attr_id">
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <el-upload
              :action="uploadUrl"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture"
              :headers="headersObj"
              :on-success="handSuccess"
              
            >
              <el-button size="small" type="primary"></el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <quill-editor v-model="addForm.goods_introduce"></quill-editor>
            <el-button style="margin-top:20px" type="primary" @click="addClick"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
      <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="30%"
        >
        <el-image 
        :src="previewPath"
        fit="fill" 
        ></el-image>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      goodId:'',
      uploadUrl:'https://www.liulongbin.top:8888/api/private/v1/upload',
      headersObj:{
        Authorization:window.sessionStorage.getItem('token')
      },
      dialogVisible:false,
      previewPath:'',
      manyTableData: [],
      onlyTableData: [],
      cateList: [],
      cateKeys: [],
      cateProps: {
        expandTrigger: "hover",
        value: "cat_id",
        label: "cat_name",
        children: "children"
      },
      activeIndex: "0",
      addForm: {
        goods_name: '',
        goods_price: '',
        goods_weight: '',
        goods_number: '',
        goods_cat: [],
        pics:[],
        goods_introduce:'',
        attrs:[]
      },
      rulesForm: {
        goods_name: [
          { required: true, message: "", trigger: "blur" }
        ],
        goods_price: [
          { required: true, message: "", trigger: "blur" }
        ],
        goods_weight: [
          { required: true, message: "", trigger: "blur" }
        ],
        goods_number: [
          { required: true, message: "", trigger: "blur" }
        ],
        goods_cat: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    };
  },
  created() {
    this.goodId = this.$route.query.id;
    if(this.goodId){
      this.getGoodData(this.goodId);
    }
    this.getCateList();
  },
  computed: {
    catId() {
      if (this.addForm.goods_cat.length !== 3) return null;
      return this.addForm.goods_cat[2];
    }
  },
  methods: {
    async getGoodData(id){
      const {data:res} = await this.$axios.get('goods/'+id);
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg);
      // this.addForm = res.data;
      console.log(res.data);
    },
    async getCateList() {
      const { data: res } = await this.$axios.get("categories");
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg);
      this.cateList = res.data;
      // console.log(this.cateList);
    },
    handleChange() {
      if (this.addForm.goods_cat.length !== 3) {
        this.addForm.goods_cat = [];
      }
      console.log(this.addForm.goods_cat);
    },
    beforeTabLeave() {
      if (this.addForm.goods_cat.length !== 3) {
        this.$message.error("");
        return false;
      }
    },
    async tabClick() {
      if (this.activeIndex == "1") {
        const { data: res } = await this.$axios.get(
          `categories/${this.catId}/attributes`,
          {
            params: { sel: "many" }
          }
        );
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg);
        res.data.forEach(item => {
          item.attr_vals = item.attr_vals ? item.attr_vals.split(" ") : [];
        });
        this.manyTableData = res.data;
        console.log(this.manyTableData);
      } else if (this.activeIndex == "2") {
        const { data: res } = await this.$axios.get(
          `categories/${this.catId}/attributes`,
          {
            params: { sel: "only" }
          }
        );
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg);
        this.$message.success(res.meta.msg);
        console.log(res.data);
        // res.data.forEach(item => {
        //   item.attr_vals = item.attr_vals ? item.attr_vals.split(" ") : [];
        // });
        this.onlyTableData = res.data;
        // console.log(this.onlyTableData);
      }
    },
    handlePreview(file){
      console.log(file)
      this.previewPath = file.response.data.url;
      this.dialogVisible = true;
    },
    handleRemove(file){
      console.log(file)
      const filePath = file.response.data.tmp_path;
      //findIndex  
      const i = this.addForm.pics.findIndex(x => x.pic === filePath);
      this.addForm.pics.splice(i,1);
      console.log(this.addForm);
    },
    handSuccess(response){
      const picInfo = {pic:response.data.tmp_path};
      this.addForm.pics.push(picInfo);
      console.log(this.addForm.pics)
    },
    addClick(){
      this.$refs.refForm.validate(async valid => {
        // console.log(valid);
        if (!valid) return this.$message.error("");

         //attrs
        this.manyTableData.forEach(item =>{
          const newData ={
            attr_id:item.attr_id,
            attr_value:item.attr_vals.join(' ')
          };
          this.addForm.attrs.push(newData);
        })
        this.onlyTableData.forEach(item =>{
          const newData ={
            attr_id:item.attr_id,
            attr_value:item.attr_vals
          };
          this.addForm.attrs.push(newData);
        })
        //goods_cat
        const form = JSON.parse(JSON.stringify(this.addForm));
        form.goods_cat = form.goods_cat.join(',');
        console.log(form)

        const { data: res } = await this.$axios.post('goods',form);
        console.log(res);
        if (res.meta.status !== 201)
          return this.$message.error("" + res.meta.msg);
        this.$message.success(res.meta.msg);
        this.$router.push('/goods');
      })
    }

  }
};
</script>
<style lang="less" scoped>
.el-checkbox {
  margin: 0 5px 0 0 !important;
}
</style>