Vue.use(VueRouter)

  const routes = [
    {path: '/', redirect: '/login'},
    {path: '/login', name: 'Login', component: Login},
    {path: '/home',name: 'Home', component: Home,redirect: '/Welcome',
      children:[
        {path: '/welcome',name: 'Welcome', component: Welcome},
        {path: '/users',name: 'Users', component: Users},
        {path: '/rights',name: 'Rights', component: Rights},
        {path: '/roles',name: 'Roles', component: Roles},
        {path: '/categories',name: 'Cate', component: Cate},
        {path: '/params',name: 'Cate_params', component: Cate_params},
        {path: '/goods',name: 'Goods_list', component: Goods_list},
        {path: '/goods/add',name: 'Add', component: Add},
        {path: '/orders',name: 'Orders', component: Orders},
        {path: '/reports',name: 'Report', component: Report},
      ],
    },
]

const router = new VueRouter({
  routes
})
router.beforeEach((to, from, next) => {
  if( to.path == '/login') next();
  const Token = window.sessionStorage.getItem('token');
  if(!Token) next('/login');
  next()
})

export default router
